import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { VehicleRouteModel } from 'src/app/shared/models/model';
import { ToastrService } from 'src/app/shared/services/toastr-message.service';
import { VechileRouteService } from 'src/app/shared/services/vechile-route.service';

@Component({
  selector: 'app-vehicle-route',
  templateUrl: './vehicle-route.component.html'
})
export class VehicleRouteComponent implements OnInit {
  vechileRouteForm!: FormGroup;
  vechileRoutes: any[] = [];
  display: boolean = false;

  constructor(private fb: FormBuilder, private vechileRouteService: VechileRouteService, private toastrService: ToastrService) {
    this.vechileRouteForm = this.fb.group({
      id : [''],
      name: ['', Validators.required],
      from: ['', Validators.required],
      to: ['', Validators.required],
    });
  }

  ngOnInit() {
    this.getVechileRoute();
  }
  getVechileRoute() {
    this.vechileRouteService.get().subscribe((response: any) => {
      this.vechileRoutes = response;
    });
  }

  updateData() {
    let temp = [...this.vechileRoutes];
    temp = JSON.parse(JSON.stringify(temp));
    this.vechileRoutes = temp;
  }

  showDialog() {
    this.display = true;
    this.vechileRouteForm.reset();
  }

  add() {
    const data = this.vechileRouteForm.value;
    if (!data.id) {
      delete data.id;
    }

    this.vechileRouteService.create(data).subscribe({
      next: (response: any) => {
        if (!!response.id) {
          this.vechileRoutes.push(response);
          this.updateData();
          this.toastrService.success('VechileRoutes Create Successfully.');
          this.display = false;
        }
      },
      error: () => {
        this.toastrService.error('VechileRoutes Create Failed.');
      }
    });
  }

  edit() {
    this.vechileRouteService.update(this.vechileRouteForm.value).subscribe((response: any) => {
      const data = this.vechileRouteForm.value;
      const index = this.vechileRoutes.findIndex(i => i.id === data.id);
      this.vechileRoutes[index] = data;
      this.toastrService.success('VechileRoutes Updated Successfully.');
      this.display = false;
    });
  }
  
  onSubmit() {
    if (this.vechileRouteForm.invalid) {
      return;
    }

    if (!this.vechileRouteForm.value.id) {
      this.add();
    } else {
      this.edit();
    }
  }

  fillForm(vechileRoute: VehicleRouteModel) {
    this.display = true;
    this.vechileRouteForm.patchValue(vechileRoute);
  }

  deleteVechile(id: number) {
    this.vechileRouteService.delete(id).subscribe((response: any) => {
      const index = this.vechileRoutes.findIndex(i => i.id == id);
      this.vechileRoutes.splice(index, 1);
      this.toastrService.success('VechileRoutes Deleted Successfully.');
    });
  }

  public close(): void {
    window.alert('Are u Sure Leave From');
    this.display = false;
  }

}
